<template>
  <div class="container">
				<el-breadcrumb-item>积分转账明细</el-breadcrumb-item>
    <page-title first-title="积分转账明细"></page-title>
    <div class="main-content">
      <div class="info-header">
        <p>店铺名称：{{ accountsSupplier.suppliers_name }}</p>
        <p>累计{{ accountsSupplier.remit_type == 'to' ? '转入' : '转出' }}： {{ accountsSupplier.all_huanbi_remit }}</p>
      </div>
      <div class="info-table">
        <el-table
          :data="accountsList"
          stripe
          v-loading="loading"
          style="width: 100%"
          :header-cell-style="{ background: '#eef1f6' }"
        >
          <el-table-column label="编号" prop="suppliers_account_log_id"></el-table-column>
          <el-table-column label="交易积分数" prop="huanbi_remit"></el-table-column>
          <el-table-column label="对方账户" prop="related_account"></el-table-column>
          <el-table-column label="收支类型">
            <template v-slot="scope">
              <span>{{ scope.row.direction == 1 ? '转入结算' : '转出结算' }}</span>
            </template>
          </el-table-column>
          <el-table-column label="日期" prop="addtime" :formatter="fmtDate"></el-table-column>
        </el-table>
      </div>
    </div>

    <div class="page-wrapper">
      <el-pagination
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        background
        layout="prev, pager, next, jumper"
        :total="total"
        :hide-on-single-page="true"
      >
      </el-pagination>
    </div>

    <div class="cancel">
      <el-button type="default" @click="$router.go(-1)"><span>返回</span></el-button>
    </div>
  </div>
</template>

<script>
import { getTransferDetailList } from '../../api/suppliers'
export default {
  name: 'HBTransferDetail',
  data() {
    return {
      accountsList: [],
      accountsSupplier: {},
      suppliers_id: this.$route.query.suppliers_id,
      loading: false,
      currentPage: 1,
      total: 0
    }
  },
  created() {
    this.getList()
  },
  methods: {
    getList() {
      this.loading = true
      getTransferDetailList(this.suppliers_id, this.currentPage).then(res => {
        this.loading = false
        this.accountsList = res.list.data
        this.total = res.list.total
        this.accountsSupplier = res.suppliers_info
      })
    },
    handleCurrentChange() {
      this.getList()
    },
    fmtDate(row, column, cellValue) {
      return this.utils.formatDate('YYYY-MM-DD HH:mm:ss', cellValue * 1e3)
    }
  }
}
</script>

<style lang="scss" scoped>
.container {
  .main-content {
    .info-header {
      display: flex;
      p {
        width: 50%;
      }
    }
  }
  .page-wrapper {
    margin-top: 15px;
    text-align: center;
  }
  .cancel {
    margin-top: 15px;
    text-align: center;
    .el-button {
      width: 100px;
    }
  }
}
</style>
